.adm-header {
  position: relative;
  padding: 20px 40px;
  background-color: #fafafa;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  &__menu {
    position: relative;
    margin: 0;
    margin-right: auto;
    padding: 0;
    display: flex;
    li {
      position: relative;
      a {
        display: block;
        padding: 13px 20px;
        box-sizing: border-box;
        font-family: 'Roboto', sans-serif;
        font-size: 14px;
        color: $blue;
        border:1px solid transparent;
      }
    }
    &--active {
      border: 1px solid $blue !important;
      box-sizing: border-box;
    }
  }
  &__user {
    display: flex;
    position: relative;
    align-items: center;
    width: 260px;
    margin-left: 40px;
    &:hover {
      img {
        border: 1px solid $someone-hover;
        transition: .4s;
      }
    }
    img {
      border: 1px solid transparent;
      transition: .4s;
      margin-right: 20px;
    }
    a {
      font-size: 14px;
      margin: 0;
      span {
        display: block;
        font-size: 12px;
        color: #000;
        margin-top: 5px;
      }
    }
  }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .adm-header__menu {
    margin-right: calc(100% - 815px);
  }
}